<template>
    <li class="song-item">
        <div class="ranking">{{index>=9?(index+1):'0'+(index+1)}}</div>
        <div class="song-box">
            <div class="song">
                <div class="song-name">
                    <span class="name">{{song.name}}</span>
                    <div class="alia" v-if="song.alia.length">
                        <span v-for="(alia,index) in song.alia" :key="index">({{alia}})</span>
                    </div>
                </div>
                <div class="song-introduce">
                    <i v-if="song.sq!==null"></i>
                    <span>{{songAuthor[0].name}}</span>
                    <span v-if="songAuthor.length>=2"> / {{songAuthor[1].name}}</span>
                    <span> - {{ song.al.name }}</span>
                </div>
            </div>
            <div class="song-play" v-if="song.id !== songId">
                <!-- <span></span> -->
            </div>
            <div class="song-play active" v-else :class="{playing:playing}">
                <span>.</span>
                <span>.</span>
                <span>.</span>
                <span>.</span>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    props: {
        song: Object,
        index: Number,
        songId: [Number, String],
        playing: Boolean,
    },
    data() {
        return {
            songAuthor: this.song.ar,
        };
    },
    components: {

    },

};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg);
    }
}

.song-item {
    margin-left: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .song-box {
        border-bottom: 1rem solid rgb(225, 222, 222);
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-grow: 1;

        .song {
            padding: 6rem 6rem 6rem 0;
            // width: 300rem;

            .song-name {
                font-size: 17rem;
                padding-bottom: 4rem;
                display: flex;
                width: 290rem;

                .name {
                    white-space: nowrap;
                    // width: 100%;
                }

                .alia {
                    color: #888;
                    margin-left: 4rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                }
            }

            .song-introduce {
                width: 280rem;
                font-size: 12rem;
                color: #888;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                word-break: normal;

                i {
                    display: inline-block;
                    width: 12rem;
                    height: 8rem;
                    margin-right: 4rem;
                    background: url(../assets/index_icon_2x.png) no-repeat;
                    background-size: 166rem 97rem;
                }
            }
        }

        .song-play {
            padding: 0 15rem;
            width: 22rem;
            height: 22rem;
            background: url(../assets/index_icon_2x.png) no-repeat;
            background-size: 166rem 97rem;
            background-position: -24rem 0;

            &.active {
                background-image: none;
                display: flex;
                justify-content: space-around;

                span {
                    display: block;
                    width: 4rem;
                    height: 100%;
                    margin: 0 1rem;
                    background-color: red;
                    animation: playingrunning 0.4s infinite alternate;
                    animation-play-state: paused;
                    transform-origin: bottom;

                    &:nth-child(1) {
                        animation-delay: 0s;
                    }

                    &:nth-child(2) {
                        animation-delay: -0.4s;
                    }

                    &:nth-child(3) {
                        animation-delay: -0.2s;
                    }

                    &:nth-child(4) {
                        animation-delay: -0.6s;
                    }
                }

                &.playing {
                    span {
                        animation-play-state: running;
                    }
                }
            }

        }
    }

    .ranking {
        width: 40rem;
        height: 100%;
        font-size: 17rem;
        color: #888;
        margin-left: -10rem;
        text-align: center;
    }
}
</style>
