<template>
  <div class="hot">
    <div class="hot-top">
      <div class="hot-pc">
        <div class="hot-icon"></div>
        <!-- <div class="hot-time">更新日期：09月20日</div> -->
        <div class="hot-time">更新日期：{{updateMonth}}月{{updateDate}}日</div>
      </div>
    </div>
    <!-- loading -->
    <div class="loading" v-if="loading"></div>
    <ul>
      <SongHot v-for="(song,index) in hotSong" :key="song.id" :index="index" :song="song" @click.native="$emit('update-song',song);$emit('update-song-list', hotSong)" :playing="playing" :songId="songId"></SongHot>
    </ul>
  </div>
</template>


<script>
import SongHot from '@/components/SongHot.vue';
export default {
  props: {
    songId: [Number, String],
    playing: Boolean,
  },
  components: {
    SongHot,
  },
  data() {
    return {
      hotSong: [],
      updateTime: "",
      updateMonth: "",
      updateDate: "",
      loading: false,
    };
  },
  created() {
    this.loading = true;
    this.axios.get("https://apis.netstart.cn/music/playlist/detail?id=3778678",).then((res) => {
      this.loading = false;
      // console.log(res.data.playlist.tracks);
      this.hotSong = res.data.playlist.tracks;
      // console.log(res.data.playlist.updateTime);
      this.updateTime = new Date(res.data.playlist.updateTime);

      this.updateMonth = this.updateTime.getMonth().length >= 2 ? (this.updateTime.getMonth() + 1) : "0" + (this.updateTime.getMonth() + 1);
      // console.log(this.updateMonth);

      this.updateDate = (this.updateTime.getDate().toString().length >= 2 ? this.updateTime.getDate() : "0" + this.updateTime.getDate()).toString();
      // console.log(this.updateTime.getDate().toString().length);
      // console.log(this.updateDate);
    });
  }
};
</script>

